@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.error-text {
  color: #e43131;
  display: block;
  height: 22px;
}
.error-text.centered {
  text-align: center;
}
.error-text.mini {
  font-size: 12px;
}
.success-text {
  color: #68dd62;
  display: block;
  height: 20px;
  margin: 12px 0;
}

.alert-box {
  position: relative;
  height: 24px;
  padding-bottom: 12px;
}
.alert-box > span {
  top: 0;
  left: 0;
  position: absolute;
  margin: 0;
}

@keyframes slideUp{0%{transform:translateY(100%)}100%{transform:translateY(0)}}button{padding:16px 32px;border-radius:100px;border:none;outline:none;height:auto;height:initial;background:none;color:#eee;font-size:1rem;display:flex;justify-content:center;cursor:pointer;transition:0.3s cubic-bezier(0.165, 0.84, 0.44, 1);font-weight:700}button svg{fill:#eee;width:24px;margin-right:16px;height:24px;transition:0.3s cubic-bezier(0.165, 0.84, 0.44, 1)}button.primary-btn{background:#38c396}button.primary-btn:hover{background:#31ab84}button.secondary-btn{border:2px solid #eee}button.secondary-btn:hover{border-color:#38c396;color:#38c396}button.refresh-btn:hover svg{fill:#38c396;transform:rotate(180deg)}button.mailbox-btn{border-bottom:3px solid #eee;border-radius:0;margin-right:24px;opacity:0.3;position:relative;max-height:52px;padding:12px 6px}button.mailbox-btn:after{content:"";height:4px;width:0%;position:absolute;bottom:0;left:0;background:#38c396;transition:0.3s cubic-bezier(0.165, 0.84, 0.44, 1)}button.mailbox-btn.active{border-color:#38c396;color:#38c396;opacity:1}button.mailbox-btn.active svg{fill:#38c396}button.mailbox-btn.active:after{width:100%}button.trash-btn svg{fill:#232931}button.trash-btn:hover svg{fill:#e43131}

@keyframes slideUp{0%{transform:translateY(100%)}100%{transform:translateY(0)}}.text-input-wrapper{border-bottom:1.5px solid rgba(238,238,238,0.3);padding:12px 0;display:flex;flex-direction:column;margin-bottom:12px}.text-input-wrapper label,.text-input-wrapper input{font-family:"Poppins", sans-serif}.text-input-wrapper label{font-weight:700;padding-right:8px;color:#38c396;font-size:0.8rem}.text-input-wrapper input{background:none;border:none;outline:none;color:#eee;font-size:1.2rem;font-weight:700}

.loader,.loader:before,.loader:after{border-radius:50%;width:2.5em;height:2.5em;animation-fill-mode:both;animation:load7 1.8s infinite ease-in-out}.loader{color:#ffffff;font-size:10px;margin:80px auto;position:relative;text-indent:-9999em;transform:translateZ(0);animation-delay:-0.16s}.loader:before,.loader:after{content:'';position:absolute;top:0}.loader:before{left:-3.5em;animation-delay:-0.32s}.loader:after{left:3.5em}@keyframes load7{0%,80%,100%{box-shadow:0 2.5em 0 -1.3em}40%{box-shadow:0 2.5em 0 0}}

@keyframes slideUp{0%{transform:translateY(100%)}100%{transform:translateY(0)}}.text-area-wrapper{padding:12px 0;display:flex;flex-direction:column;margin-bottom:12px}.text-area-wrapper label,.text-area-wrapper textarea{font-family:"Poppins", sans-serif}.text-area-wrapper label{font-weight:700;padding-right:8px;color:#38c396;font-size:0.8rem}.text-area-wrapper textarea{margin-top:12px;background:none;border:none;outline:none;color:#eee;font-size:1rem;font-weight:500;resize:none;border-bottom:1.5px solid rgba(238,238,238,0.3);min-height:180px}

@keyframes slideUp{0%{transform:translateY(100%)}100%{transform:translateY(0)}}.message-prompt-wrapper{position:absolute;bottom:0;right:10%;min-width:400px;box-sizing:border-box;min-height:700px;background:#393e46;animation:slideUp 0.6s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;z-index:30;display:flex;flex-direction:column;padding:40px}.message-prompt-wrapper .message-prompt-top-row{display:flex;justify-content:space-between;align-items:center}.message-prompt-wrapper .message-prompt-top-row .close-btn{padding:0;margin-left:auto}.message-prompt-wrapper .message-prompt-top-row .close-btn:hover svg{fill:#e43131}.message-prompt-wrapper .message-prompt-top-row .close-btn svg{width:16px;margin-right:0}.message-prompt-wrapper .message-heading{margin:0;color:#eee}@media screen and (max-width: 414px){.message-prompt-wrapper{top:0;left:0;min-width:100vw;min-height:100vh}}

