@keyframes slideUp{0%{transform:translateY(100%)}100%{transform:translateY(0)}}.message-wrapper{width:100%;padding:8px 0;transition:0.3s cubic-bezier(0.165, 0.84, 0.44, 1);opacity:0.3;display:flex;align-items:center;justify-content:space-between;box-sizing:border-box;cursor:pointer}.message-wrapper .text-content{box-sizing:border-box;width:100%;height:100%}.message-wrapper .text-content .message-body-text{color:#eee;font-size:0.8rem;display:inline-block;padding-left:12px}.message-wrapper .text-content .message-body-text.from,.message-wrapper .text-content .message-body-text.to,.message-wrapper .text-content .message-body-text.date{width:170px;font-weight:400}.message-wrapper .text-content .message-body-text.topic{font-weight:700;width:400px;font-size:1rem}.message-wrapper .trash-btn{opacity:0;position:relative;z-index:3}.message-wrapper:hover,.message-wrapper.open{background:#393e46;opacity:1}.message-wrapper:hover .trash-btn,.message-wrapper.open .trash-btn{opacity:1}.message-wrapper.active{background:#2d9b78;opacity:1}.message-content{border-bottom:1px solid #eeeeee33;max-height:0;overflow:hidden;transition:0.4s cubic-bezier(0.165, 0.84, 0.44, 1);height:100px}.message-content.open{max-height:100px}.message-content .message-text{padding:12px 12px 0 12px;display:block;max-width:400px;color:#eee;font-size:0.8rem}@media screen and (max-width: 414px){.message-wrapper .text-content .message-body-text{width:100% !important;word-break:break-word}.message-wrapper .text-content .message-body-text.hideOnMobile{display:none}}

@keyframes slideUp{0%{transform:translateY(100%)}100%{transform:translateY(0)}}.mailbox-page-wrapper{height:100vh;max-width:1170px;margin:0 auto}.mailbox-page-wrapper nav{padding-top:60px}.mailbox-page-wrapper .label-row{margin-bottom:12px}.mailbox-page-wrapper .label-row .list-label{display:inline-block;padding-left:12px;color:#eee;font-size:1.1rem}.mailbox-page-wrapper .label-row .list-label.from,.mailbox-page-wrapper .label-row .list-label.to,.mailbox-page-wrapper .label-row .list-label.date{width:170px;font-weight:400}.mailbox-page-wrapper .label-row .list-label.topic{font-weight:700;width:400px}.mailbox-page-wrapper .messages-list{max-height:60vh;overflow-y:auto;padding-right:6px}.mailbox-page-wrapper .messages-list::-webkit-scrollbar-track{background-color:none}.mailbox-page-wrapper .messages-list::-webkit-scrollbar{width:6px;background-color:none}.mailbox-page-wrapper .messages-list::-webkit-scrollbar-thumb{background-color:#393e46}@media screen and (max-width: 1024px){.mailbox-page-wrapper{padding:0 24px}.mailbox-page-wrapper .label-row{display:none}}@media screen and (max-width: 414px){.mailbox-page-wrapper{max-width:100vw;padding:0}.mailbox-page-wrapper nav{padding-top:24px}.mailbox-page-wrapper nav .Logo{margin:0 auto;display:block;width:120px}.mailbox-page-wrapper .label-row{display:none}}

